// --- ðŸ‘‡ GRÃFICO DE GASTOS ATUALIZADO COM CORES SÃ“BRIAS E SEM ARREDONDAMENTO ðŸ‘‡ ---
function ExpensePieChart({ expenses }) {
  // Processar os dados para o grÃ¡fico
  const categorySpending = expenses.reduce((acc, expense) => {
    const categoryName = expense.category?.name || 'Sem Categoria';
    acc[categoryName] = (acc[categoryName] || 0) + expense.amount;
    return acc;
  }, {});

  const labels = Object.keys(categorySpending);
  const dataValues = Object.values(categorySpending);
  const total = dataValues.reduce((sum, val) => sum + val, 0);
  
  // Paleta de cores sÃ³brias e profissionais
  const soberColors = [
    '#3B82F6', '#10B981', '#F59E0B', '#EF4444', '#8B5CF6',
    '#06B6D4', '#84CC16', '#F97316', '#6366F1', '#EC4899',
    '#14B8A6', '#F43F5E', '#0EA5E9', '#22C55E', '#EAB308'
  ];

  const borderColors = ['#1E293B']; // Bordas escuras uniformes
  const hoverColors = soberColors.map(color => color);

  const chartData = {
    labels: labels,
    datasets: [
      {
        data: dataValues,
        backgroundColor: soberColors.slice(0, labels.length),
        borderColor: borderColors,
        borderWidth: 2,
        hoverBackgroundColor: hoverColors.slice(0, labels.length),
        hoverBorderColor: '#FFFFFF',
        hoverBorderWidth: 3,
        spacing: 0, // Remove espaÃ§amento entre segmentos
        hoverOffset: 15,
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    cutout: '60%',
    layout: {
      padding: 15
    },
    plugins: {
      legend: {
        position: 'right',
        labels: {
          color: '#E2E8F0',
          font: {
            size: 11,
            weight: '500',
            family: "'Inter', sans-serif"
          },
          boxWidth: 14,
          boxHeight: 14,
          padding: 12,
          usePointStyle: true,
          pointStyle: 'circle',
        },
        onHover: function(event, legendItem) {
          event.native.target.style.cursor = 'pointer';
        },
        onLeave: function(event, legendItem) {
          event.native.target.style.cursor = 'default';
        },
      },
      tooltip: {
        backgroundColor: 'rgba(15, 23, 42, 0.95)',
        titleColor: '#F1F5F9',
        bodyColor: '#CBD5E1',
        titleFont: {
          size: 12,
          weight: '600'
        },
        bodyFont: {
          size: 11,
          weight: '500'
        },
        borderColor: '#334155',
        borderWidth: 1,
        cornerRadius: 8,
        padding: 12,
        displayColors: true,
        usePointStyle: true,
        boxPadding: 4,
        callbacks: {
          label: function (context) {
            const label = context.label || '';
            const value = context.parsed;
            const percentage = total > 0 ? ((value / total) * 100).toFixed(1) : 0;
            return `${label}: ${formatCurrency(value)} (${percentage}%)`;
          },
          title: function (context) {
            return context[0].label;
          }
        },
      },
    },
    interaction: {
      intersect: false,
      mode: 'index',
    },
    animation: {
      animateScale: true,
      animateRotate: true,
      duration: 800,
      easing: 'easeOutCubic'
    },
    elements: {
      arc: {
        borderJoinStyle: 'bevel', // Remove bordas arredondadas
      }
    }
  };

  // EstatÃ­sticas para o card de resumo
  const topCategory = Object.entries(categorySpending).reduce((max, [cat, amount]) => 
    amount > max.amount ? { category: cat, amount } : max, 
    { category: '', amount: 0 }
  );

  if (expenses.length === 0) {
    return (
      <div className="bg-gradient-to-br from-fin-card/60 to-fin-dark/60 p-6 rounded-2xl border border-white/10 shadow-xl backdrop-blur-sm h-full flex flex-col items-center justify-center">
        <div className="text-center">
          <div className="text-6xl mb-4">ðŸ“Š</div>
          <h2 className="text-xl font-bold text-white mb-2">Gastos por Categoria</h2>
          <p className="text-gray-400 text-lg">Sem gastos para exibir</p>
          <p className="text-sm text-gray-500 mt-1">Registre despesas para ver a distribuiÃ§Ã£o</p>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-gradient-to-br from-fin-card/60 to-fin-dark/60 p-6 rounded-2xl border border-white/10 shadow-xl backdrop-blur-sm">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-bold text-white flex items-center gap-3">
          <span className="bg-gradient-to-r from-fin-gold to-fin-highlight p-2 rounded-xl">ðŸ“Š</span>
          DistribuiÃ§Ã£o de Gastos
        </h2>
        <div className="text-xs text-gray-400 bg-black/30 px-3 py-2 rounded-full border border-white/10">
          {labels.length} {labels.length === 1 ? 'categoria' : 'categorias'}
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 items-center">
        {/* GrÃ¡fico */}
        <div className="lg:col-span-2 relative" style={{ height: '300px' }}>
          <Doughnut data={chartData} options={chartOptions} />
          
          {/* Centro do grÃ¡fico com informaÃ§Ãµes */}
          <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
            <div className="text-center">
              <div className="text-2xl font-bold text-white mb-1">
                {formatCurrency(total)}
              </div>
              <div className="text-xs text-gray-400 font-medium">
                Total Gasto
              </div>
              <div className="text-xs text-fin-gold mt-1">
                {labels.length} categorias
              </div>
            </div>
          </div>
        </div>

        {/* Cards de estatÃ­sticas */}
        <div className="lg:col-span-1 space-y-4">
          <div className="bg-gradient-to-br from-fin-dark/80 to-fin-card/60 p-4 rounded-xl border border-white/10">
            <h3 className="text-sm font-semibold text-gray-300 mb-3">ðŸ“ˆ Maior Gasto</h3>
            <div className="text-lg font-bold text-white truncate" title={topCategory.category}>
              {topCategory.category}
            </div>
            <div className="text-fin-gold font-semibold text-sm">
              {formatCurrency(topCategory.amount)}
            </div>
            <div className="text-xs text-gray-400 mt-1">
              {total > 0 ? ((topCategory.amount / total) * 100).toFixed(1) : 0}% do total
            </div>
          </div>

          <div className="bg-gradient-to-br from-fin-dark/80 to-fin-card/60 p-4 rounded-xl border border-white/10">
            <h3 className="text-sm font-semibold text-gray-300 mb-3">ðŸ“‹ Resumo</h3>
            <div className="space-y-2 text-sm">
              <div className="flex justify-between">
                <span className="text-gray-400">Total:</span>
                <span className="text-white font-medium">{formatCurrency(total)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-400">Categorias:</span>
                <span className="text-fin-gold font-medium">{labels.length}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-400">TransaÃ§Ãµes:</span>
                <span className="text-white font-medium">{expenses.length}</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Legenda mobile */}
      <div className="lg:hidden mt-6">
        <div className="grid grid-cols-2 gap-2 max-h-32 overflow-y-auto">
          {labels.map((label, index) => (
            <div key={label} className="flex items-center gap-2 text-xs">
              <div 
                className="w-3 h-3 rounded-full flex-shrink-0"
                style={{ backgroundColor: soberColors[index] }}
              ></div>
              <span className="text-gray-300 truncate" title={label}>
                {label}
              </span>
              <span className="text-fin-gold font-medium ml-auto">
                {total > 0 ? ((categorySpending[label] / total) * 100).toFixed(0) : 0}%
              </span>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}